import React, { useState } from 'react';
import SearchBox from './SearchBox';
import axios from 'axios';
import AnimateError from './validation/AnimateError';
import ValidateEmail from './validation/EmailValid';

export default function Contact() {
    const [name, setName] = useState('');
    const [email, setEmail] = useState('');
    const [subject, setSubject] = useState('');
    const [message, setMessage] = useState('');
    const [conterr, setConterr] = useState({
        name: '',
        email: '',
        subject: '',
        message: ''
    });
    const [disable, setDisable] = useState(false);
    const [mortSucc, setMortSucc] = useState(false);

    const contact = (e) => {
        e.preventDefault();
        let errorCount = 0;
        if(name === '') {
            errorCount++;
            setConterr((prevState)=> {
                return {...prevState, name: 'Name is Required'}
            });
            AnimateError();
        } else if(name.length < 2 || !isNaN(name)){
            errorCount++;
            setConterr((prevState)=> {
                return {...prevState, name: 'Invalid Name'}
            });
            AnimateError();
        } else {
            setConterr((prevState)=> {
                return {...prevState, name: ''}
            });
        } if(email === '') {
            errorCount++;
            setConterr((prevState)=> {
                return {...prevState, email: 'Email is Required'}
            });
            AnimateError();
        } else if(ValidateEmail(email)){
            errorCount++;
            setConterr((prevState)=> {
                return {...prevState, email: 'Email ID not valid'}
            });
            AnimateError();
        } else {
            setConterr((prevState)=> {
                return {...prevState, email: ''}
            });
        } if(subject === '') {
            errorCount++;
            setConterr((prevState)=> {
                return {...prevState, subject: 'Subject is Required'}
            });
            AnimateError();
        } else if(subject.length < 2 || !isNaN(subject)){
            errorCount++;
            setConterr((prevState)=> {
                return {...prevState, subject: 'Invalid Subject'}
            });
            AnimateError();
        } else {
            setConterr((prevState)=> {
                return {...prevState, subject: ''}
            });
        } if(message === '') {
            errorCount++;
            setConterr((prevState)=> {
                return {...prevState, message: 'Message is Required'}
            });
            AnimateError();
        } else if(message.length < 2 || !isNaN(message)){
            errorCount++;
            setConterr((prevState)=> {
                return {...prevState, message: 'Invalid Subject'}
            });
            AnimateError();
        } else {
            setConterr((prevState)=> {
                return {...prevState, message: ''}
            });
        }
        if(errorCount === 0) {
            let contData = { name, email, subject, message };
            setDisable(true);
            axios.post('api/contactpost', contData).then((res) => {
                setMortSucc(true);
                setTimeout(function() {
                    setMortSucc(false);
                    setDisable(false);
                }, 2000);
            })
            .catch((err) => {
                setMortSucc(false);
                setDisable(false);
            });
        }
    }
    return (
        <>
            <section className="page-top-section set-bg" style={{ backgroundImage: "url('/img/page-top-bg.jpg')"}}>
                <div className="page-top-warp">
                <SearchBox />
                </div>
            </section>
            
            <section className="contact-section">
                <div className="container">
                    <div className="row">
                        <div className="col-lg-5">
                            <div className="section-title text-left">
                                <h2>Get in touch</h2>
                            </div>
                            <div className="contact-text">
                                <p>Donec eget efficitur ex. Donec eget dolor vitae eros feugiat tristique id vitae massa. Proin vulputate congue rutrum. Fusce lobortis a enim eget tempus. Class aptent taciti sociosqu ad litora torquent per conubia nostra, per inceptos himenaeos. Suspendisse potenti. Ut gravida mattis magna.</p>
                            </div>
                        </div>
                        <div className="col-lg-7">
                            <div className="row">
                                <div className="col-sm-4">
                                    <div className="contact-info-box">
                                        <div className="ci-icon">
                                            <span>ND</span>
                                        </div>
                                        <h4>New Delhi</h4>
                                        <p>2768 XYZ Road New Delhi, ND 001100 </p>
                                    </div>
                                </div>
                                <div className="col-sm-4">
                                    <div className="contact-info-box">
                                        <div className="ci-icon">
                                            <span>MB</span>
                                        </div>
                                        <h4>Mumbai</h4>
                                        <p>2768 XYZ Road Mumbai, MB 001101</p>
                                    </div>
                                </div>
                                <div className="col-sm-4">
                                    <div className="contact-info-box">
                                        <div className="ci-icon">
                                            <span>BB</span>
                                        </div>
                                        <h4>Bhubaneswar</h4>
                                        <p>2768 XYZ Road Bhubaneswar, BB 001102</p>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                    <form className="contact-form" autoComplete="off" onSubmit={contact}>
                        <div className="row">
                            <div className="col-lg-4">
                                <div className="form_wr_input">
                                    <input className="form_input_style" onChange={(e)=>setName(e.target.value)} type="text" placeholder="Your name" />
                                    <span className="form_err">{conterr.name}</span>
                                </div>
                            </div>
                            <div className="col-lg-4">
                                <div className="form_wr_input">
                                    <input className="form_input_style" type="text" onChange={(e)=>setEmail(e.target.value)} placeholder="Your e-mail" />
                                    <span className="form_err">{conterr.email}</span>
                                </div>
                            </div>
                            <div className="col-lg-4">
                                <div className="form_wr_input">
                                    <input className="form_input_style" type="text" onChange={(e)=>setSubject(e.target.value)} placeholder="Subject" />
                                    <span className="form_err">{conterr.subject}</span>
                                </div>
                            </div>
                            <div className="col-lg-12">
                                <div className="form_wr_input">
                                    <textarea className="form_input_style" onChange={(e)=>setMessage(e.target.value)} placeholder="Message"></textarea>
                                    <span className="form_err">{conterr.message}</span>
                                </div>
                                <button className="site-btn sb-big" disabled={disable}>Send message</button>
                                {mortSucc?<p className="success_msge">Message send successfully.</p>:""}
                            </div>
                        </div>
                    </form>
                </div>
            </section>
        </>
    )
}
